  margin-bottom: 30px;
  margin-right: -20px;
  margin-left: -20px;
}
.video-center:hover {
  box-shadow: 15px 0 30px rgba(0, 0, 0, 0.5);
}
.video-center .play-icon {
  position: absolute;
  left: 24px;
  bottom: 32px;
}
.video-center,
.play-icon {
  cursor: pointer;
}
.video-center--active .svg,
.play-icon--active .svg,
.video-center:hover .svg,
.play-icon:hover .svg,
.video-center:focus .svg,
.play-icon:focus .svg,
