  font-family: 'Playfair Display', serif;
  margin-bottom: 45px;
}
.reviews-item {
  width: 260px;
  position: relative;
  margin-bottom: 25px;
  border-bottom: 1px solid #e7e7e7;
  padding-bottom: 15px;
}
.reviews-item:before {
  position: absolute;
  top: 0;
  left: -18px;
  content: "";
  display: block;
  width: 36px;
  height: 29px;
  z-index: -1;
  background-image: url("/src/img/icon_quotes.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.review-item__text {
  font-size: 16px;
  line-height: 1.9;
  font-weight: 400;
  color: #999999;
  font-style: italic;
  margin-bottom: 10px;
}
.review-item__author {
  font-size: 16px;
  line-height: 1.9;
  font-weight: 900;
  color: #000000;
}
.review-item__author:before {
  content: "- ";
  color: #407fa2;
}
@media (min-width: 630px) {
  .reviews {
    flex-direction: row;
  }
