  font-family: 'Playfair Display', serif;
  margin-bottom: 25px;
  text-align: center;
  width: 100%;
}
.video-item {
  position: relative;
  overflow: hidden;
  margin-bottom: 80px;
  cursor: pointer;
  box-shadow: 15px 0 20px rgba(0, 0, 0, 0.5);
}
.video-item:hover {
  box-shadow: 15px 0 30px rgba(0, 0, 0, 0.5);
}
