  position: relative;
  width: 56px;
  height: 56px;
  background-color: white;
  border-radius: 50px;
}
.play-icon .svg {
  position: absolute;
  left: 50%;
  top: 50%;
  display: block;
  margin-left: -4px;
  margin-top: -5.5px;
}
.video-item .play-icon {
  position: absolute;
  left: -10px;
  bottom: -15px;
  cursor: pointer;
}
.video-item .play-icon--active .svg,
.video-item .play-icon:hover .svg,
.video-item .play-icon:focus .svg,
.video-item .play-icon:active .svg {
  fill: #407fa2;
}
.video-center {
  order: -1;
  position: relative;
  cursor: pointer;
  box-shadow: 15px 0 20px rgba(0, 0, 0, 0.5);
  margin-bottom: 30px;
  margin-right: -20px;
  margin-left: -20px;
}
.video-center:hover {
  box-shadow: 15px 0 30px rgba(0, 0, 0, 0.5);
}
.video-center .play-icon {
  position: absolute;
  left: 24px;
  bottom: 32px;
}
.video-center,
.play-icon {
  cursor: pointer;
}
.video-center--active .svg,
.play-icon--active .svg,
.video-center:hover .svg,
.play-icon:hover .svg,
.video-center:focus .svg,
.play-icon:focus .svg,
.video-center:active .svg,
.play-icon:active .svg {
  fill: #407fa2;
}
.video-item,
.play-icon {
  cursor: pointer;
}
.video-item--active .svg,
.play-icon--active .svg,
.video-item:hover .svg,
.play-icon:hover .svg,
.video-item:focus .svg,
.play-icon:focus .svg,
.video-item:active .svg,
.play-icon:active .svg {
  fill: #407fa2;
}
